import { Link } from "gatsby";
import React from "react";
import ctaWave from "../../assets/images/cta/ctaWave.svg";
import CtaBg from "../../assets/images/cta/ctaImg1.png";

import "./master.css";

const CallToAction = () => {
  return (
    <div className="cta-section">
      <div className="cta-wave">
        <img src={ctaWave} alt="" />
      </div>
      <div className="cta-wrapper">
        <div className="container">
          <div className="cta-heading">
            <h3>
            Get in touch with us <span>Today!</span>
            </h3>
            <p>
            We are here to help you find the right tile for you.
            </p>
            <Link to="/contactUs">Enquire Now</Link>
          </div>
          <div className="cta-img-wrapper">
            <div className="cta-img">
              <img src={CtaBg} alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CallToAction;
