import React from "react";
import { useLocation } from "@reach/router";
import { products } from "../../mockData";
import { Nav, Navbar } from "react-bootstrap";
import logo from "../../assets/images/header/logo.svg";
// import searchicon from "../../assets/images/header/searchicon.svg";
import fb from "../../assets/images/home/fb.svg";
import insta from "../../assets/images/home/insta.svg";
import "./header.css";
import { Link } from "gatsby";

const Header = () => {
  const location = useLocation();

  return (
    <div className='header'>
      <div className='top-header'>
        <div className='container'>
          <div className='top-menu-grid'>
            <div className='header-social'>
              <ul>
                <li>
                  <Link to='/'>
                    <img src={fb} alt='' />
                  </Link>
                </li>
                <li>
                  <Link to='/'>
                    <img src={insta} alt='' />
                  </Link>
                </li>
              </ul>
            </div>
            <div className='logo'>
              <Link to='/'>
                <img src={logo} alt='' />
              </Link>
            </div>
            <div className='header-contact'>
              <div className='call-icon'>
                <a href='tel:07 3219 8882'>
                  <CallIcon />
                  <h6>Call Us: 07 3219 8882</h6>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Navbar collapseOnSelect expand='lg'>
        <div className='container'>
          <div className='nam-menu-grid'>
            <Navbar.Toggle aria-controls='responsive-navbar-nav' />
            <Navbar.Collapse id='responsive-navbar-nav'>
              <Nav className='mr-auto'>
                <Nav.Link href='/' className={location.pathname === "/" ? "active" : ""}>
                  Home
                </Nav.Link>
                <Nav.Link href='/about' className={location.pathname === "/about/" ? "active" : ""}>
                  About Us
                </Nav.Link>
                <div className='mega-link'>
                  <Nav.Link
                    href='/products'
                    className={location.pathname === "/products/" ? "active" : ""}>
                    Products
                  </Nav.Link>
                  <MegaMenu />
                </div>
                <Nav.Link
                  href='/gallery'
                  className={location.pathname === "/gallery/" ? "active" : ""}>
                  Gallery
                </Nav.Link>
                <Nav.Link href='/blogs' className={location.pathname === "/blogs/" ? "active" : ""}>
                  Blog
                </Nav.Link>
                <Nav.Link
                  href='/contactUs'
                  className={location.pathname === "/contactUs/" ? "active" : ""}>
                  Contact Us
                </Nav.Link>
              </Nav>
            </Navbar.Collapse>
            {/* <div className="search-icon">
              <img src={searchicon} alt="" />
            </div> */}
          </div>
        </div>
      </Navbar>
    </div>
  );
};
export default Header;

